import React from "react";
import { graphql } from "gatsby";
import { GatsbyImage, getImage } from "gatsby-plugin-image";
import Layout from "../components/layout";
import { truncateHTML } from './truncateHTML';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import IconButton from '@material-ui/core/IconButton';
import FacebookIcon from '@material-ui/icons/Facebook';
import TwitterIcon from '@material-ui/icons/Twitter';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import EmailIcon from '@material-ui/icons/Email';
import { useLocation } from "@reach/router";
import Grid from '@material-ui/core/Grid';
import Divider from '@material-ui/core/Divider';
import Tooltip from '@material-ui/core/Tooltip';
import Typography from '@material-ui/core/Typography';


import cheerio from 'cheerio';



import { useState, useEffect } from "react";
import { Link } from "gatsby";
import Button from '@material-ui/core/Button';




const useStyles = makeStyles((theme) => ({
  image: {
    width: '120%',
    marginLeft: '-10%',
    height: 'auto',
    objectFit: 'cover',
  },
  content: {
    maxWidth: '800px',
    margin: '0 auto',
  },
  shareButtons: {
    [theme.breakpoints.up('lg')]: {
      textAlign: 'right',
    },
    [theme.breakpoints.down('md')]: {
      textAlign: 'right',
    },
    [theme.breakpoints.down('sm')]: {
      textAlign: 'center',
    },
  },
  shareButton: {
    margin: theme.spacing(1),
    color: 'darkred',
    '&:hover': {
      color: 'red',
    },
  },
  tooltip: {
    fontSize: '1em',
  },


  caption: {
    display: 'block',
    textAlign: 'center',
    color: 'grey',
    fontStyle: 'italic',
    marginBottom: '1em', // change this to adjust the gap after the caption
  },
  

  customButton: {
    backgroundColor: 'white', // Background color
    border: '1px solid red', // Red border
    color: 'black', // Text color
    '&:hover': {
      color: 'white', // Text color on hover
      backgroundColor: 'rgb(47, 102, 151)', // Background color on hover
    },
  },




}));

const convertDate = (inputFormat) => {
  const splitDate = inputFormat.split("/");
  const months = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];
  const month = months[parseInt(splitDate[1]) - 1];
  return `${month} ${splitDate[0]}, ${splitDate[2]}`;
}

export default function BlogPost({ data }) {
  const classes = useStyles();
  const post = data.markdownRemark;
  const articleType = post.frontmatter.article;
  
  const location = useLocation();



  const [content, setContent] = useState(post.html);
const [isMagazine, setIsMagazine] = useState(false);








// image caption code..
useEffect(() => {
  const $ = cheerio.load(post.html);
  $('img').each(function() {
    const caption = $(this).attr('alt');
    $(this).after(`<figcaption class="${classes.caption}">${caption}</figcaption>`);
  });
  setContent($.html());
}, [post.html]);






  const shareToSocial = (platform) => {
    const postUrl = window.location.origin + location.pathname;
    const postTitle = encodeURIComponent(post.frontmatter.title);

    let url;
    switch(platform) {
      case 'facebook':
        url = `https://www.facebook.com/sharer/sharer.php?u=${postUrl}`;
        break;
      case 'twitter':
        url = `https://twitter.com/share?url=${postUrl}&text=${postTitle}`;
        break;
      case 'linkedin':
        url = `https://www.linkedin.com/shareArticle?mini=true&url=${postUrl}&title=${postTitle}`;
        break;
      case 'email':
        url = `mailto:?subject=${postTitle}&body=${postUrl}`;
        break;
      default:
        url = '';
    }

    window.open(url, '_blank');
  }

  useEffect(() => {
    if (articleType === 'Magazine') {
      setContent(prevContent => truncateHTML(prevContent, 200));
      setIsMagazine(true);
    } else {
      setIsMagazine(false);
    }
  }, [articleType]);
  

  return (
    <Layout>
      <Container className={classes.content}>
        <Grid container>
          <Grid item xs={12}>
            <h1>{post.frontmatter.title}</h1>
            <h3>{post.frontmatter.summary}</h3>
            <GatsbyImage
              image={getImage(post.frontmatter.image)}
              alt={post.frontmatter.title}
              className={classes.image}
            />
            <Grid item xs={12} className={classes.shareButtons}>
              <Tooltip title={<Typography className={classes.tooltip}>Share with Facebook</Typography>} placement="top">
                <IconButton onClick={() => shareToSocial('facebook')} color="primary" className={classes.shareButton}>
                  <FacebookIcon />
                </IconButton>
              </Tooltip>
              <Tooltip title={<Typography className={classes.tooltip}>Share with Twitter</Typography>} placement="top">
                <IconButton onClick={() => shareToSocial('twitter')} color="primary" className={classes.shareButton}>
                  <TwitterIcon />
                </IconButton>
              </Tooltip>
              <Tooltip title={<Typography className={classes.tooltip}>Share with LinkedIn</Typography>} placement="top">
                <IconButton onClick={() => shareToSocial('linkedin')} color="primary" className={classes.shareButton}>
                  <LinkedInIcon />
                </IconButton>
              </Tooltip>
              <Tooltip title={<Typography className={classes.tooltip}>Mail to anyone</Typography>} placement="top">
                <IconButton onClick={() => shareToSocial('email')} color="primary" className={classes.shareButton}>
                  <EmailIcon />
                </IconButton>
              </Tooltip>
            </Grid>
            <Divider />
            <Grid container direction="row" justify="space-between" alignItems="center">
              <Grid item xs>
                <h5>{post.frontmatter.author}</h5>
              </Grid>
              <Grid item xs style={{textAlign: 'right'}}>
                <em>{convertDate(post.frontmatter.time)}</em>
              </Grid>
            </Grid>
            <div dangerouslySetInnerHTML={{ __html: content }} />
            {isMagazine && (
               <div style={{display: 'flex', justifyContent: 'center'}}>
                <Button variant="contained" color="primary" component={Link} to="/subscription" className={classes.customButton}>
                  Subscribe to read more
               </Button>
                </div>
              )}
            <Divider />
          </Grid>
        </Grid>
      </Container>
    </Layout>
  )
}

export const query = graphql`
  query($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      frontmatter {
        title
        author
        time
        summary
        article
        image {
          childImageSharp {
            gatsbyImageData(
              layout: CONSTRAINED
              placeholder: BLURRED
              aspectRatio: 2.00
              width: 800
            )
          }
        }
      }
    }
  }
`;
