import cheerio from 'cheerio';


const cloneNode = (node, $) => {
    if (node.type === 'text') {
      return $.parseHTML(node.data)[0];
    } else if (node.type === 'tag') {
      const clonedNode = $('<' + node.name + '>')[0];
      for (const attr in node.attribs) {
        $(clonedNode).attr(attr, node.attribs[attr]);
      }
      return clonedNode;
    }
  };
  
  export const truncateHTML = (html, targetWordCount) => {
    const $ = cheerio.load(html);
    const $truncated = cheerio.load('<body></body>');
  
    let wordCount = 0;
  
    const truncateNode = (node, parent) => {
      const clonedNode = cloneNode(node, $);
      $truncated(clonedNode).appendTo(parent);
  
      if (node.type === 'text') {
        const words = node.data.split(' ');
        if (wordCount + words.length > targetWordCount) {
          words.length = targetWordCount - wordCount;
          clonedNode.data = words.join(' ') + '...';
          wordCount = targetWordCount;
        } else {
          wordCount += words.length;
        }
      } else if (node.type === 'tag' && node.name !== 'img') {
        for (const child of node.children) {
          if (wordCount >= targetWordCount) break;
          truncateNode(child, clonedNode);
        }
      }
    };
  
    for (const child of $('body')[0].children) {
      if (wordCount >= targetWordCount) break;
      truncateNode(child, $truncated('body'));
    }
  
    return $truncated('body').html();
  };
  